@import url('https://fonts.googleapis.com/css2?family=Grape+Nuts&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300&display=swap');
@media (min-width: 320px) and (max-width: 480px) {
  body {
    background-color: #a59ef0;
  }
  .giff {
    width: 100px;
    height: 100px;
  }
  .rt {
    border: thick double #32a1ce;
    width: auto;
  }
  .content {
    border: 2px solid darkblue;
    border-radius: 7px;
    min-height: 50px;
    max-width: 100%;
    height: auto;
    position: absolute;
    z-index: 12;
    margin-top: 10px;
    margin-right: 8px;
  }
  .shine-button {
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    position: relative;
    overflow: hidden;
    border: 2px solid #fe6637;
    background-color: #f1daa5;
    border-radius: 8px;
    font-family: 'Source Code Pro', monospace;
    color: black;
    transition: .2s ease-in-out;
  }
  .shine-button:before {
    content: "";
    background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .5));
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
  }
  .shine-button:hover {
    background: #fe6637;
    color: #fff;
  }
  .shine-button:hover:before {
    left: 150px;
    transition: .5s ease-in-out;
  }
  .modal__close {
    width: 20px;
    background-color: red;
    color: white;
    cursor: pointer;
  }
  .modal.open {
    top: 0;
    left: 0;
  }
  .modal.close {
    height: 0;
    display: none;
  }
  .flex-header{
    
    background-color: #f08eed;
    display: flex;
    justify-content: space-between;
    font-family: 'Grape Nuts', cursive;
  }
  .content-text{
    background-color: #f0b2ed;
    font-family: 'Grape Nuts', cursive;
  }
  .header-text{
    margin-left: 10px;
    font-weight: bold;

  }
  .image-icon{
    height: 15px;
    width: 15px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .hello-text{
    font-family: 'Caveat', cursive;
    font-size: 23px;
    background-color: #c817e8;
  }
  .hello-header-text{
    font-size: 24px;
    font-family: 'Caveat', cursive;
    font-weight: bold;
    background-color: #ae17e8;
  }
  .hello-buton{
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    overflow: hidden;
    border: 2px solid red;
    border-radius: 8px;
    font-family: 'Caveat', cursive;
    background-color: red;
    color: white;
    font-weight: bold;
    font-size: 20px;
  }
  .content-text-button{
    background-color: #c817e8;
    display: flex;
    justify-content: center;
  }
  .box{
    font-family: 'Cormorant', serif;
    font-weight: bold;
  }
  .header-h4{
    text-align: center;
    font-family: 'Cormorant', serif;
    font-size: 18px;
    font-weight: bolder;
    margin-bottom: 15px;
  }
}
